.table-wrapper {
    overflow-y: hidden;

    table {
        width: 100%;
        min-width: 400px;
        border-spacing: 0;
    }
    
    thead {
        background-color: var(--second-bg);
    }
    
    tr {
        text-align: left;
    }
    
    th, td {
        text-transform: capitalize;
        padding: 15px 10px;
    }
    
    tbody > tr:hover {
        background-color: var(--main-color);
        color: var(--txt-white);
    }
}

.table__pagination {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 20px;
}

.table__pagination-item ~ .table__pagination-item {
    margin-left: 10px;
}

.table__pagination-item {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.table__pagination-item.active, .table__pagination-item.active:hover {
    background-color: var(--main-color);
    color: var(--txt-white);
    font-weight: 600;
}

.table__pagination-item :hover {
    color: var(--txt-white);
    background-color: var(--second-color);
}
